@font-face {

    font-family: "Montserrat-bold";
    src: url("./assets/fonts/Montserrat/Montserrat-Bold.ttf");
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
}
@font-face {

    font-family: "Montserrat-Regular";
    src: url("./assets/fonts/Montserrat/Montserrat-Regular.ttf");
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
  }
  @font-face {

    font-family: "Montserrat-Light";
    src: url("./assets/fonts/Montserrat/Montserrat-Light.ttf");
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
  }

  .toggle-block{
    position: absolute;
    top: 4rem;
    left: 45%;
  }

  .toggle-btn {
    font-family: 'Courier New';
    position: relative;
    font-size: 1rem;
    cursor: pointer;
}
  .toggle-btn:hover,:visited{
    color: fuchsia;
  }

  .toggle-btn:before {
    content: "";
    width: 32px;
    height: 46px;
    background-image: url("./assets/svg/exchange-arrow.svg");
    background-repeat: no-repeat;
    background-size: 34px;
    position: absolute;
    bottom: -0.72rem;
    left: -2.5rem;
}

@media only screen and (max-width: 767px) {
  .toggle-block {
    position: absolute;
    top: 2rem;
    right: 0;
}


}