.project-gif {
  width: 31rem;
  margin-right: 2rem;
}
.vertical-bar {
  color: fuchsia;
  font-weight: bold;
  margin-right: 0.3rem;
  margin-left: 0.3rem;
}
.lateral-menu {
  width: 200px;
  height: 600px;
  background-color: fuchsia;
  opacity: 100%;
  position: relative;
  z-index: 99;
}
.mobile-item {
  color: white;
}
.lateral-cross {
  font-size: 32px;
  color: white;
  font-weight: bolder;
  margin-left: 44%;
}
.lateral-items {
  text-align: center;
  color: white;
}

.github-link {
  color: fuchsia;
  text-decoration: none;
}

.technologies-used {
  color: fuchsia;
  margin-right: 0.5rem;
}

.project-txtcontent {
  display: flex;
  flex-direction: column;
  margin-left: 3rem;
  margin-right: 3rem;
  text-align: left;
}
.projects-header {
  text-align: center;
  margin-bottom: 6rem;
}

.project-landscape {
  display: flex;
  flex-direction: column;
  margin-left: 4rem;
}

.landscape-block {
  display: flex;
  margin-bottom: 7rem;
  width: 75%;
}

.project-gif--vertical {
  height: 30rem;
  margin-bottom: 6rem;
}

.project-main-title {
  text-align: center;
  font-family: "Montserrat-Regular";
  margin-top: 7rem;
}
.text-links {
  display: flex;
  flex-direction: column;
  position: relative;
  bottom: 2.3rem;
  left: 2rem;
}

.projects-container {
  display: flex;
  justify-content: space-between;
  width: 93%;
  flex-wrap: nowrap;
  margin-left: 4rem;
}
.projects-main {
  font-family: "Montserrat-Light";
  line-height: 1.5rem;
  letter-spacing: 0.1rem;
}
.cv-sidebar {
  display: flex;
}
.right-arrow {
  width: 50px;
}
.download-text {
  font-weight: normal;
  font-family: "Courier New", Courier, monospace;
  cursor: pointer;
}
.question-logo {
  width: 25px;
  transform: rotate(-362deg);
  margin-right: 0.5rem;
}
.translation-block {
  display: flex;
  font-family: "Courier New";
  position: absolute;
  left: 1rem;
  cursor: pointer;
}
.header-links {
  position: absolute;
  justify-content: space-evenly;
  display: flex;
  width: 100%;
  top: 3rem;
}
.cv-download {
  position: absolute;
  right: 5rem;
}

