@media only screen and (min-width: 768px) {
  /* tablets and desktop */
}

@media only screen and (max-width: 767px) {
  /* phones */
  .landscape-block {
    display: flex;
    margin-bottom: 7rem;
    width: 75%;
    flex-direction: column;
  }
  .project-txtcontent {
    width: 100%;
  }
  .mobile-menu {
    position: fixed;
    left: 1rem;
    top: 1.5rem;
  }
  .cv-download {
    position: absolute;
    right: 3rem;
  }
  .header-links {
    position: absolute;
    display: block;
    top: 25%;
  }
  .translation-block {
    position: absolute;
    cursor: pointer;
    left: 1rem;
  }
  .mobile-menu {
    position: relative;
    left: 1rem;
    top: 1.5rem;
  }
  .right-sidebar__txt-container {
    display: none;
  }
  .projects-container {
    align-items: center;
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    margin: 0 auto;
    justify-content: center;
  }
  .project-gif {
    width: 23rem;
    margin: 0 auto;
  }
  .project-txtcontent {
    width: 100%;
    margin: 0 auto;
  }
  .project-gif--vertical {
    height: 30rem;
    margin-bottom: 1rem;
  }
  .project-landscape {
    margin-left: 0rem;
  }
  .landscape-block {
    margin-bottom: 2rem;
    width: 101%;
  }
}
@media only screen and (max-width: 767px) and (orientation: portrait) {
  /* portrait phones */
}
