.contact-main {
    font-family: 'Montserrat-Light';
    font-weight: normal;
}

.contact-title{
    text-align: center;
    margin-top: 10rem;
    margin-bottom: 7rem;
}

.contact-container{
    display: flex;
    justify-content: space-between;
    width: 80%;
    margin: 0 auto;
}
.text-links{
  display: flex;
    flex-direction: column;
    position: relative;
    bottom: 2.3rem;
    left: 2rem;
}

.contact-btn {
    background-color: transparent;
    color: inherit;
    font-weight: bolder;
    padding: 1em 1.5em;
    text-decoration: none;
    text-transform: uppercase;
    border: 2px solid fuchsia;
}

.contact-btn:hover {
    background-color: fuchsia;
    color: white;
  }
  
  .contact-btn:active {
    background-color: fuchsia;
  }
  
  .contact-btn:visited {
    background-color: fuchsia;
  }

  .cv-sidebar {
    display: flex;
  }
  .right-arrow{
    width: 50px;
  }
  .download-text {
    font-weight: normal;
    font-family: 'Courier New', Courier, monospace;
    cursor: pointer;
  
  }
  .question-logo {
    width: 25px;
    transform: rotate(
  -362deg
  );
  margin-right: 0.5rem;
  }
  .lateral-menu {
    width: 200px;
    height: 600px;
    background-color: fuchsia;
    opacity: 100%;
    position: relative;
    z-index: 99;
  }
  .mobile-item {
    color: white;
  }
  .lateral-cross {
    font-size: 32px;
    color: white;
    font-weight: bolder;
    margin-left: 44%;
  }
  .lateral-items {
    text-align: center;
    color: white;
  }
  .translation-block {
    display: flex;
    font-family: 'Courier New';
    position: absolute;
    left: 1rem;
    cursor: pointer;
  }
  .header-links{
      position: absolute;
      justify-content: space-evenly;
      display: flex;
      width: 100%;
      top: 3rem;
  }
  .cv-download {
    position: absolute;
    right: 5rem;
  }
  @media only screen and (min-width: 768px) {
    /* tablets and desktop */
  }
  
  @media only screen and (max-width: 767px) {
    /* phones */
  
    .mobile-menu {
      position: fixed;
      left: 1rem;
      top: 1.5rem;
  }
    .cv-download {
      position: absolute;
      right: 3rem;
  }
  .header-links {
    position: absolute;
    display: block;
    top: 25%;
  }
  .translation-block{
    position: absolute;
      cursor: pointer;
      left: 1rem;
  }
 
.right-sidebar__txt-container {
  display: none;
}
.mobile-menu-contact {
  position: absolute;
  top: 2rem;

}
