  text-align: center;
  margin-top: 10rem;
  margin-bottom: 2rem;
}
.contact-main {
  position: relative;
  top: 4rem;
  width: 73%;
  margin: auto;
}
.contact-btn {
  margin-bottom: 1rem;
}

