


.intro-text{
    font-family: 'Montserrat-Regular';
    text-align: center;
    transform: translateY(120%);
    margin: 2rem;
    font-size: 1rem;
    line-height: 1.9rem;
}

.intro-text__title--smaller{
  font-size: 0.999rem;
  font-weight: 400;
}


.change {
  animation: change 1.5s ease-in-out infinite;
}

@keyframes change {
  0% {
    color: red
  }
  10% {
    color: yellow
  }
  15% {
    color: fuchsia
  }
  25% {
    color: greenyellow
  }
  75% {
    color: cadetblue
  }
}

.pdflogo{
  width: 28px;
}


.cv-sidebar {
  display: flex;
}
.right-arrow{
  width: 50px;
}
.download-text {
  font-weight: normal;
  font-family: 'Courier New', Courier, monospace;
  cursor: pointer;

}
.mobile-menu{
  display: none;
  position: relative;
}
.mobile-menu::before {
  content: "";
  width: 26px;
  height: 25px;
  background-image: url("../../assets/svg/hamburguer.svg");
  background-repeat: no-repeat;
  background-size: 25px;
  position: absolute;
  left: 0;
}
.text-links{
  display: flex;
    flex-direction: column;
    position: relative;
    bottom: 2.3rem;
    left: 2rem;
}
.download-text:hover{
  color: fuchsia;
}
.right-sidebar__txt {
  font-size: 1.39rem;
  text-align: center;
  margin-right: 4rem;
  font-family: 'Montserrat-Light';
  font-weight: normal;

}

.right-sidebar__container {
  text-align: left;
  padding-top: 2rem;

}
.question-logo {
  width: 25px;
  transform: rotate(
-362deg
);
margin-right: 0.5rem;
margin-bottom: 1.2rem;
}
.hidden-sidebar{
  display: none;
}

.translation-block {
  display: flex;
  font-family: 'Courier New';
  position: absolute;
  left: 1rem;
  cursor: pointer;
}
.lateral-menu {
  width: 200px;
  height: 600px;
  background-color: fuchsia;
  opacity: 100%;
  position: relative;
  z-index: 99;
}
.lateral-cross {
  font-size: 32px;
  color: white;
  font-weight: bolder;
  margin-left: 44%;
}
.mobile-item {
  color: white;
}
.lateral-items {
  text-align: center;
  color: white;
}
.header-links{
    position: absolute;
    justify-content: space-evenly;
    display: flex;
    width: 100%;
    top: 3rem;
}
.right-sidebar {
  display: block;
  margin: 0 auto;
  line-height: 27px;
  letter-spacing: 0.09rem;
  padding-top: 4rem;
  font-size: 33px;
  transform: translateY(68%);
  animation: fadein 5s;
  position: relative;
  left: 3rem;

  -webkit-animation: fadein 8s; 
  -moz-animation: fadein 5s;
   -ms-animation: fadein 5s;
    -o-animation: fadein 5s; 
       animation: fadein 5s;
}

.right-sidebar a{
  color: inherit;
}

.right-sidebar a:hover{
  color: fuchsia;
}
@keyframes fadein {
from { opacity: 0; }
to   { opacity: 1; }
}
.translation-block:hover{
  color: fuchsia;
}

.right-sidenar__title {
  font-weight: 600;
    font-size: 32px;
    text-align: right;
}
a {
  text-decoration: none;
  color: none !important;
}
.right-sidebar__txt-container {
  display: flex;
}
.cv-download {
  position: absolute;
  right: 5rem;
}
.right-sidebar__txt:nth-child(4) {
  width: 23%;
}
.right-sidebar__txt:nth-child(5) {
  width: 23%;
}

@media only screen and (min-width: 768px) {
  /* tablets and desktop */
}

@media only screen and (max-width: 767px) {
  /* phones */

  .mobile-menu{
    display: block;
  }
  .cv-download {
    position: absolute;
    right: 3rem;
}
.header-links {
  position: absolute;
  display: block;
  top: 25%;
}
.translation-block{
  position: absolute;
    cursor: pointer;
    left: 1rem;
}

.mobile-menu {
  position: relative;
  left: 1rem;
  top: 1.5rem;
}
.right-sidebar__txt-container {
  display: none;
}
}
@media only screen and (max-width: 767px) and (orientation: portrait) {
  /* portrait phones */
}