.aboutme-title{
    text-align: center;
    font-family: 'Montserrat-Regular';
    margin-top: 8rem;
}

.pinkme{
    width: 400px;
}
.about-container{
    display: flex;
    width: 102%;
    margin-left: 2rem;
}
.text-links{
  display: flex;
    flex-direction: column;
    position: relative;
    bottom: 2.3rem;
    left: 2rem;
}
.aboutme-txt__container {
    width: 60%;
    margin-left: 3rem;
    font-size: 20px;
    font-family: 'Montserrat-Light'
}
.cv-sidebar {
    display: flex;
  }
  .right-arrow{
    width: 50px;
  }
  .download-text {
    font-weight: normal;
    font-family: 'Courier New', Courier, monospace;
    cursor: pointer;
  
  }
  .question-logo {
    width: 25px;
    transform: rotate(
  -362deg
  );
  margin-right: 0.5rem;
  }
  .translation-block {
    display: flex;
    font-family: 'Courier New';
    position: absolute;
    left: 1rem;
    cursor: pointer;
  }
  .header-links{
      position: absolute;
      justify-content: space-evenly;
      display: flex;
      width: 100%;
      top: 3rem;
  }
  .lateral-menu {
    width: 200px;
    height: 600px;
    background-color: fuchsia;
    opacity: 100%;
    position: relative;
    z-index: 99;
  }
  .mobile-item {
    color: white;
  }
  .lateral-cross {
    font-size: 32px;
    color: white;
    font-weight: bolder;
    margin-left: 44%;
  }
  .lateral-items {
    text-align: center;
    color: white;
  }
  .cv-download {
    position: absolute;
    right: 5rem;
  }

  @media only screen and (min-width: 768px) {
    /* tablets and desktop */
  }
  
  @media only screen and (max-width: 767px) {
    /* phones */
  
    .mobile-menu {
      position: fixed;
      left: 1rem;
      top: 1.5rem;
  }
  

